import React from "react";

export default function MissionKey() {
  return (
    <div className="my-3">
      <p>
        <span className="px-3 mr-2 bg-success">
          Mission in Green means Success
        </span>
      </p>
      <p>
        <span className="px-3 mr-2 bg-danger">Mission in Red means Fail</span>
      </p>
    </div>
  );
}
