import React, { Component } from "react";
import NotFound from "../../src/404_not_found.png";
import { Link } from "react-router-dom";

export default class NotFoundPage extends Component {
  render() {
    return (
      <div>
        <img
          src={NotFound}
          alt="404 error"
          style={{
            width: 800,
            display: "block",
            margin: "auto",
          }}
        />
        <Link to="/" className="btn btn-secondary">
          Back to Home Page
        </Link>
      </div>
    );
  }
}
